.navbar {
    height:7vh;
    width:100%;
    background:#D32F2F;
    .navbar-ul{
        width: 100%;
        height: 100%;
        list-style: none;
        @include flex(flex, row, nowrap);
        .nav-left{
            height: 100%;
            width: 60%;
            background:green;
            align-self: flex-start;
            @include flex(flex, row,  nowrap);
            a{
                display: block;
                height:7vh;
                width: 10%;
                text-decoration: none;
                line-height:7vh;
            }
        }
        .nav-right{
            height: 100%;
            width: 40%;
            background-color: purple;
            align-self: flex-end;
        }
    }
}